.s5 {
  user-select: none;
  .container {
    padding: 0;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    height: 100%;
    line-height: 0;
    font-size: 0;
  }

  .plan {
    position: relative;
    width: 846px;
    height: 626px;
    overflow: hidden;
  }

 

  .plan-map-container {
    position: relative;
    height: 100%;
    width: 846px;
    height: 626px;
  }

  // .map-wrapper {
  //   position: absolute;
  //   left: 50%;
  //   top: 50%;
  //   transform: translate(-50%, -50%);
  // }

  .plan-marker {
    position: absolute;
    // border-radius: 65% 0% 65% 45%;
    // transform: rotate(135deg);
    // background-color: #ffffff;
    width: 19px;
    height: 24px;
    background: url(../img/map_marker.png) no-repeat 50% 50%; 
        background-size: 14px 23px;
    color: #000000;
    opacity: 0;
    visibility: hidden;
    margin-top: -20px;
    transition: all .15s ease-in-out;

    &.active {
      opacity: 1;
      visibility: visible;
      margin-top: 0;
    }
  }

  .plan-marker-number {
    @extend %stReg;
    display: block;
        width: 19px;
        height: 16px;
    line-height: 16px;
    color: #73829E;
        letter-spacing: -0.7px;
    font-size: 10px;
    // transform: rotate(225deg);
    // margin-top: 9px;
    // margin-left: 2px;
    text-align: center;
  }

  .zoom-container {
    position: absolute;
    top: 20px;
    right: 18px;
    z-index: 1;
  }

  .zoom-btn {
    width: 42px;
    height: 42px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(255,255,255, .85);
    border-radius: 3px;
    cursor: pointer;

    &:hover {
      opacity: 1;
    }

    svg {
      width: 23px;
      height: 23px;
      fill: #525252;
    }
  }

   .zoom-out {
    display: none;
  }

  .zoom-in, .plan-settings {
    margin-bottom: 8px;
  }

  .plan-settings {
    display: none;
  }

  .plan-scheme {
    background-color: #F5F5F5;
    display: flex;
    flex-direction: column;
    line-height: 1;
    width: 414px;
    padding: 35px 37px 0;
  }
  .plan-close {
    display: none;
  }


  h2 {
    text-align: left;
    margin-bottom: 22px;
  }

  .header-type {
    @extend %sdReg;
    font-size: 17px;
    line-height: 23px;
    letter-spacing: 0.43px;
    color: #8C8C8C;
    margin-bottom: 21px;
  }

  .header-type:last-of-type {
    margin-bottom: 9px;
  }


  .plan-type {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 30px;
  }

  .plan-type-item {
    margin-bottom: 19px;
    min-width: 160px;

    &.active-all {
      .checkbox-custom {
        border: 2px solid #F3914E;

        &:before {
          content: "";
          display: block;
          position: absolute;
          top: 3px;
          right: 3px;
          bottom: 3px;
          left: 3px;
          background: #F3914E;
          border-radius: 3px;
        }
      }
    }

    &.active-not-all {
      .checkbox-custom {

        &:before {
          content: "";
          display: block;
          position: absolute;
          top: 3px;
          right: 3px;
          bottom: 3px;
          left: 3px;
          background: #F3914E;
          border-radius: 3px;
        }
      }
    }
  }

  .plan-type-item label {
    cursor: pointer;
    display: flex;
    flex-direction: row;

    &:hover {
      .label-txt {
        color: #525252;
      }
    }
  }

  .checkbox {
    display: none;

    &:checked+.checkbox-custom {
      border: 2px solid #F3914E;

      &:before {
        content: "";
        display: block;
        position: absolute;
        top: 3px;
        right: 3px;
        bottom: 3px;
        left: 3px;
        background: #F3914E;
        border-radius: 3px;
      }
    }

    &:checked~.label-txt {
      color: #525252;
    }
  }

  .checkbox-custom {
    position: relative;
    display: block;
    width: 21px;
    height: 21px;
    border: 2px solid #8C8C8C;
    border-radius: 5px;
  }

  .label-txt {
    @extend %sdReg;
    font-size: 15px;
    line-height: 23px;
    letter-spacing: 0.38px;
    color: #8C8C8C;
    margin-left: 9px;
  }

  .plan-type-btn {
    width: 149px;
    margin-right: 10px;
    @extend %sdReg;
    font-size: 15px;
    line-height: 23px;
    letter-spacing: 0.38px;
    color: #FFFFFF;
    background: linear-gradient(90deg, rgba(184, 184, 184, 1) 0%, rgba(184, 184, 184, 1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#B8B8B8', endColorstr='#B8B8B8', GradientType=1);
    border-radius: 5px;


    &:hover {
      background: #8C8C8C;
    }
  }


  .plan-number {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-bottom: 6px;
  }
  .plan-number-item {
    @extend %sdReg;
    font-size: 16px;
    line-height: 33px;
    letter-spacing: 0.48px;
    color: #525252;
    margin: 0 3px 4px 0;
    width: 25px;
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    cursor: pointer;

    &:hover {
      background: #B8B8B8;
    }

    &.checked {
      color: #ffffff;
      background: #F3914E;
    }
  }

  .plan-number-btns {
    display: flex;
    justify-content: space-between;
  }
  .plan-number-btn {
    @extend .plan-type-btn;
  }
}




